import uniList from '../../components/uni-list-c/uni-list.vue'
import uniListItem from '../../components/uni-list-item-c/uni-list-item.vue'
import amap from '../../common/amap-wx.js'
import getcode from '../../wxcomponents/getcode/getcode.vue'
import xlocation from '../../wxcomponents/xlocation/xlocation.vue'
const formatDate = require('../../util/util.js')
export default {
	data() {
		var dateObj = new Date()
		var currentTime = dateObj.getTime()
		var timer = formatDate.formatDateTime((currentTime + 1000 * 2000))
		return {
			province:'',
			city:'',
			district:'',
			township:'',
			timer: timer,
			title: '验证码：',
			name: '',
			manager: '',
			phone: '',
			getCode:'',
			area: '',
			street: '',
			detail_address: '',
			businessTypeItem: '个人',
			repairTypeItem: '上门维修',
			businessType: [
				'个人', '公司'
			],
			repairType: [
				'上门维修', '到店维修'
			],
			index: 0
		}
	},
	methods: {
		onSave() {
			if(this.name == ''){
				uni.showToast({
					title: '请填写商家名称',
					icon:'none'
				})
			}else if (this.manager == '') {
				uni.showToast({
					title:'请填写负责人',
					icon:'none'
				})
			}else if (this.phone == ''){
				uni.showToast({
					title: '请填写手机号码',
					icon:'none'
				})
			}else if(this.getCode == ''){
				uni.showToast({
					title: '请获取验证码',
					icon:'none'
				})
			}else if(this.detail_address == ''){
				uni.showToast({
					title: '请填写详细地址',
					icon:'none'
				})
			}else{
				this.onPost()
				uni.showToast({
					title: '保存成功'
				})
				setTimeout(() => {
					uni.hideToast()
					uni.navigateTo({
						url: 'upload-photo'
					})
				}, 2000)
			}
		},
		onType(e) {
			let num = e.detail.value
			this.businessTypeItem = this.businessType[num]
		},
		onRepairType(e) {
			let num = e.detail.value
			this.repairTypeItem = this.repairType[num]
		},
		onName(e) {
			this.name = e.detail.value
		},
		onManager(e) {
			this.manager = e.detail.value
		},
		onPhone(e) {
			this.phone = e.detail.value
		},
		onArea(e) {
			this.area = e.district
			this.street = e.street,
			this.detail_address = e.detailAddress
		},
		bindCode(e){
			console.log(e.codeText)
			this.getCode = e.codeText
		},
		onPost() {
			uni.request({
				url:'https://120.24.180.246/xmRepair/shopInfo/add',
				method:'POST',
				data:{
					name: this.name,
					manager:this.manager,
					phone:this.phone,
					area:this.area,
					street:this.street,
					detail_address:this.detail_address,
					type:this.businessTypeItem,
					service_mode:this.repairTypeItem,
					stat:0
				},
				success: res=>{
					console.log('success')
				}
			})
		}
	},
	components: {
		getcode,
		uniList,
		uniListItem,
		xlocation
	}
}
